import { ViewController } from 'ionic-angular/navigation/view-controller';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams ,Events} from 'ionic-angular';
import { ContactProvider } from './../../../providers/contact-provider'; 
import {FormGroup,FormControl} from '@angular/forms';
import { SqliteProvider } from './../../../providers/sqlite-provider';
/**
 * Generated class for the ContactTagChooserPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-contact-tag-chooser',
  templateUrl: 'contact-tag-chooser.html',
})
export class ContactTagChooserPage {
  friendTagList:any;
  choosenTag:string;
  tagForm;
  contact:any;
  constructor(public navCtrl: NavController, 
    public contactProvider: ContactProvider,
    public viewCtrl:ViewController,
    public events:Events,
    public sqliteProvider:SqliteProvider,
    public navParams: NavParams) {

    this.contact=this.navParams.data;

    this.choosenTag=this.contact.friendTagId;
    /** 
    this.contactProvider.FriendTagList().subscribe(res=>{ 
      this.friendTagList = res.result;  
    })*/
    
    //从sql表里获取
    this.sqliteProvider.queryFriendsGroups().subscribe((data)=>{
      this.friendTagList=[];
      for(let tag of data){
        if(tag.GroupId != -1){
          let group={id:tag.GroupId,name:tag.GroupName}
          this.friendTagList.push(group)       
        }
      }
    });


  }

  openContactGroupManage()  {
    this.navCtrl.push('ContactGroupManagePage');
  } 
  
  doSubmit() {
    //console.log(this.choosenTag);
    for(let item of this.friendTagList){
      if(this.choosenTag == item.id){
        this.contact.friendTagId = item.id;
        this.contact.friendTagName = item.name
      } 
    }
    this.contactProvider.FriendSetting(this.contact.id,
      [{
        "Name":"FriendTagId",
        "value":this.choosenTag
     }]
      ).subscribe(res=>{
       this.sqliteProvider.updateFriendInfo(this.contact).subscribe(()=>{
          this.events.publish('contact: refresh-friendtag',this.contact) 
       });
       
    })
    this.viewCtrl.dismiss();  
  }

  ionViewDidLoad() {
  }

}



